import { Currency, CurrencyAmount, Fraction, Percent } from '@uniswap/sdk'
import React from 'react'
import { Text } from 'rebass'
import { ButtonPrimary } from '../../components/Button'
import { RowBetween, RowFixed } from '../../components/Row'
import CurrencyLogo from '../../components/CurrencyLogo'
import { Field } from '../../state/mint/actions'
import { TYPE } from '../../theme'

export function ConfirmAddModalBottom({
  noLiquidity,
  price,
  currencies,
  parsedAmounts,
  poolTokenPercentage,
  onAdd
}: {
  noLiquidity?: boolean
  price?: Fraction
  currencies: { [field in Field]?: Currency }
  parsedAmounts: { [field in Field]?: CurrencyAmount }
  poolTokenPercentage?: Percent
  onAdd: () => void
}) {
  const Atoen = currencies[Field.CURRENCY_A]?.symbol
  const Btoen = currencies[Field.CURRENCY_B]?.symbol
  let AtoenTxt = Atoen
  let BtoenTxt = Btoen
  if(Atoen ==='ETH'){
    AtoenTxt = 'ASM'
  }
  if(Btoen ==='ETH'){
    BtoenTxt = 'ASM'
  }
  return (
    <>
      <RowBetween>
        <TYPE.body>存入{AtoenTxt}</TYPE.body>
        <RowFixed>
          <CurrencyLogo currency={currencies[Field.CURRENCY_A]} style={{ marginRight: '8px' }} />
          <TYPE.body>{parsedAmounts[Field.CURRENCY_A]?.toSignificant(6)}</TYPE.body>
        </RowFixed>
      </RowBetween>
      <RowBetween>
        <TYPE.body>存入{BtoenTxt}</TYPE.body>
        <RowFixed>
          <CurrencyLogo currency={currencies[Field.CURRENCY_B]} style={{ marginRight: '8px' }} />
          <TYPE.body>{parsedAmounts[Field.CURRENCY_B]?.toSignificant(6)}</TYPE.body>
        </RowFixed>
      </RowBetween>
      <RowBetween>
        <TYPE.body>汇率</TYPE.body>
        <TYPE.body>
          {`1 ${AtoenTxt} = ${price?.toSignificant(4)} ${
            BtoenTxt
          }`}
        </TYPE.body>
      </RowBetween>
      <RowBetween style={{ justifyContent: 'flex-end' }}>
        <TYPE.body>
          {`1 ${BtoenTxt} = ${price?.invert().toSignificant(4)} ${
            AtoenTxt
          }`}
        </TYPE.body>
      </RowBetween>
      <RowBetween>
        <TYPE.body>流动性占比:</TYPE.body>
        <TYPE.body>{noLiquidity ? '100' : poolTokenPercentage?.toSignificant(4)}%</TYPE.body>
      </RowBetween>
      <ButtonPrimary style={{ margin: '20px 0 0 0' }} onClick={onAdd}>
        <Text fontWeight={500} fontSize={20}>
          {noLiquidity ? '创建资金池 & 添加' : '确认添加'}
        </Text>
      </ButtonPrimary>
    </>
  )
}
